@font-face {
  font-family: 'DINPro';
  src: local("DINPro"), url("../fonts/DINPro.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'DINPro';
  src: local("DINPro"), url("../fonts/DINPro-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'DINPro-Black';
  src: local("DINPro-Black"), url("../fonts/DINPro-Black.woff") format("woff");
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy';
  src: local("Gilroy"), url("../fonts/Gilroy-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy';
  src: local("Gilroy"), url("../fonts/Gilroy-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

.about {
  background-color: #F7F5FA;
  padding-bottom: 5%;
}

.about__list {
  display: flex;
  flex-wrap: wrap;
  background-color: #fff;
  margin: -100px 7% 0;
}

.about__item {
  padding: 50px 60px 59px;
  border: 1px solid #EDEDED;
  width: 50%;
}

.about__title {
  font-size: 12px;
  line-height: 19px;
  text-transform: uppercase;
  color: #F47821;
  margin-bottom: 12px;
  letter-spacing: 0.04em;
}

.about__info {
  font-size: 24px;
  line-height: 30px;
  color: #222222;
}

.advantages {
  background-color: #F7F5FA;
  margin-top: 106px;
  padding: 120px 0 163px 0;
}

.advantages .container {
  width: 1170px;
}

.advantages__title {
  line-height: 50px;
  text-align: center;
  margin-bottom: 59px;
}

.advantages__list {
  display: flex;
}

.advantages__item {
  margin-right: 23px;
}

.advantages__item:last-child {
  margin-right: 0;
}

.advantages__icon {
  display: inline-block;
  width: 52px;
  height: 52px;
  margin-bottom: 30px;
}

.advantages__icon-pic {
  max-width: 100%;
  max-height: 100%;
}

.advantages__main {
  font-size: 20px;
  font-family: 'DINPro';
  font-weight: 700;
  line-height: 25px;
  color: #000000;
  margin-bottom: 25px;
}

.advantages__main:after {
  content: '';
  display: block;
  margin-top: 24px;
  background: #F47821;
  width: 25px;
  height: 2px;
}

.advantages__info {
  font-family: Roboto;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #4E4D53;
}

.banner {
  width: 100%;
  height: 600px;
}

.banner__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.form {
  background-color: #161616;
  padding-top: 98px;
  padding-bottom: 120px;
}

.form__title {
  line-height: 52px;
  /* identical to box height */
  text-align: center;
  margin-bottom: 63px;
}

.form__title.title {
  color: #FFFFFF;
}

.form__wrapp {
  height: 100%;
  width: 100%;
  padding: 0 16.6%;
}

.form__row {
  margin-bottom: 49px;
  display: flex;
}

.form__row--nonemb {
  margin-bottom: 0;
}

.form__number {
  width: 64px;
  height: 64px;
  border: 1px solid;
  border-color: rgba(255, 255, 255, 0.23);
  border-radius: 50%;
  position: relative;
  margin-right: 29px;
  flex-shrink: 0;
}

.form__number-value {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: Roboto;
  font-weight: 500;
  font-size: 20px;
  color: #F47821;
}

.form__label {
  text-align: left;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.form__main {
  font-size: 16px;
  line-height: 25px;
  /* identical to box height */
  color: #FFFFFF;
  margin-top: 20px;
}

.form__input {
  border: none;
  outline: none;
  font-family: Roboto;
  font-weight: 400;
  font-size: 24px;
  line-height: 35px;
  color: #FFFFFF;
  opacity: 0.3;
  padding-bottom: 16px;
  margin-top: 20px;
  background-color: transparent;
  letter-spacing: -0.01em;
  width: 100%;
  border-bottom: 2px solid #F7F8FA;
}

.form__textarea {
  resize: none;
}

.form__button {
  width: 100%;
  margin-top: 59px;
  padding: 0 94px;
}

.intro {
  text-align: center;
  padding: 168px 0 121px;
  background-color: #fff;
}

.intro__title {
  margin-bottom: 20px;
  font-size: 48px;
  line-height: 54px;
  letter-spacing: -0.02em;
}

.intro__info {
  font-family: 'Roboto';
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #4A4556;
  margin-bottom: 33px;
}

.intro__info span {
  color: #F47821;
}

.intro__button .button {
  margin-left: 31px;
}

.similar {
  margin-top: 83px;
}

.similar__wrapp {
  height: 100%;
  width: 100%;
  padding: 0 16%;
}

.similar__title {
  font-size: 48px;
  line-height: 60px;
  color: #222222;
  margin-bottom: 22px;
  letter-spacing: -0.02em;
}

.similar__info {
  font-family: Roboto;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #4E4D53;
  margin-bottom: 28px;
}

.similar__info:last-child {
  margin-bottom: 0;
}

/*base styles*/

* {
  box-sizing: border-box;
  transition: .5s;
}

html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  font-family: 'DINPro', sans-serif;
  font-size: 14px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: #fff;
}

.wrapper {
  position: relative;
  min-height: 100%;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  background-color: #fff;
}

.section {
  height: 100vh;
  width: 100vw;
}

.container {
  width: 1140px;
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
}

.button {
  outline: none;
  border: 1px solid transparent;
  border-radius: 111px;
  background: transparent;
  font-family: 'Gilroy';
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  color: #222222;
  letter-spacing: 0.02em;
  transition: .4s;
  padding-right: 32px;
  cursor: pointer;
}

.button:before {
  content: '+';
  display: inline-block;
  vertical-align: middle;
  color: #F47821;
  font-size: 32px;
  line-height: 54px;
  font-weight: 400;
  background: rgba(135, 135, 135, 0.05);
  border-radius: 50%;
  height: 54px;
  width: 54px;
  text-align: center;
  margin-right: -13px;
  transition: .4s;
}

.button:hover {
  background: rgba(135, 135, 135, 0.05);
}

.button:hover:before {
  background-color: transparent;
}

.button--submit {
  width: 100%;
  padding: 18px 0;
  background: #FFFFFF;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */
  letter-spacing: 0.04em;
  color: #F47821;
}

.button--submit:before {
  display: none;
}

.overflow {
  overflow: hidden;
}

.title {
  font-size: 40px;
  color: #000000;
  letter-spacing: -0.02em;
}

.error {
  color: red;
  font-size: 20px;
}

input,
textarea {
  background-color: transparent !important;
}

/*base end*/

@media screen and (max-width: 1200px) {
  .container {
    padding: 0 3%;
  }
}

@media screen and (max-width: 950px) {
  .form__wrapp {
    padding: 0 10%;
  }
}

@media screen and (max-width: 768px) {
  .about__list {
    margin: -100px 0 0;
  }

  .about__item {
    padding: 50px 30px 59px;
  }

  .about__info {
    font-size: 20px;
  }

  .advantages__list {
    flex-wrap: wrap;
  }

  .advantages__item {
    width: 47%;
    margin-bottom: 20px;
  }

  .advantages__item:nth-child(2) {
    margin-right: 0;
  }

  .advantages__item:last-child {
    margin-bottom: 0;
  }

  .form__wrapp {
    padding: 0;
  }

  .form__textarea {
    min-height: 130px;
    overflow-y: hidden;
  }

  .form__input {
    font-size: 20px;
  }

  .similar__wrapp {
    padding: 0 5%;
  }
}

@media screen and (max-width: 480px) {
  .about__item {
    width: 100%;
  }

  .about__info {
    font-size: 18px;
  }

  .advantages__item {
    width: 100%;
    margin-right: 0;
    text-align: center;
  }

  .advantages__main:after {
    display: none;
  }

  .form__title {
    padding: 0 15px;
  }

  .form__number {
    width: 45px;
    height: 45px;
  }

  .form__main {
    margin-top: 0;
  }

  .form__textarea {
    min-height: 150px;
  }

  .form__button {
    padding: 0 30px;
  }

  .intro {
    padding: 120px 0;
  }

  .intro__title {
    font-size: 36px;
  }

  .intro__info {
    font-size: 14px;
  }

  .title {
    font-size: 30px;
    line-height: 40px;
  }
}

@media screen and (max-width: 320px) {
  .intro__title {
    font-size: 30px;
    line-height: 40px;
  }

  .intro__info {
    font-size: 14px;
  }
}